import React, { useState, useEffect } from 'react';
import {
	InputDiv,
	LabelStyle,
	InputStyle,
	ErrorText,
} from '../common/Input.style';
import FormWrap from '../common/FormWrap.style';
import { BigBtnDiv, BigBtn } from '../common/Button.style';
import { useNavigate } from 'react-router-dom';

function JoinForm() {
	const [id, setId] = useState('');
	const [pw, setPw] = useState('');

	const [isDisabled, setIsDisabled] = useState(true);
	const [isWrong, setIsWrong] = useState(false);
	const [msg, setMsg] = useState('');
	const [msgEmail, setMsgEmail] = useState('');
	const [isEmail, setIsEmail] = useState(true);
	const [isPw, setIsPw] = useState(true);

	const url = 'https://mandarin.api.weniv.co.kr';

	const loginData = {
		user: {
			email: id,
			password: pw,
		},
	};

	const navigate = useNavigate();

	// id, pw       
	useEffect(() => {
		if (id && pw) {
			setIsDisabled(false);
		} else {
			setIsDisabled(true);
		}
	}, [id, pw]);

	// useEffect    
	useEffect(() => {
		if (isEmail) {
			setIsEmail(true);
		} else {
			setIsEmail(false);
		}
	}, [id, pw]);

	// id, pw        
	function checkBtn() {
		if (id !== '' && pw !== '') {
			setIsDisabled(false);
		} else {
			setIsDisabled(true);
		}
	}

	//   
	function emailValidation(target) {
		const emailRegex =
			/([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
		const emailCurrent = target;
		if (emailCurrent === '') {
			setMsgEmail('  .');
			setIsEmail(false);
		} else if (!emailRegex.test(emailCurrent)) {
			setMsgEmail('  .');
			setIsEmail(false);
		} else {
			setMsgEmail('');
			setIsEmail(true);
		}
	}

	//   
	function pwValidation(target) {
		const pwCurrent = target;
		if (pwCurrent === '') {
			setMsg('  .');
			setIsPw(false);
		} else if (pwCurrent.length < 6) {
			setMsg(' 6  .');
			setIsPw(false);
		} else {
			setMsg('');
			setIsPw(true);
		}
	}

	// API 
	const handleJoinSubmit = async (e) => {
		e.preventDefault();
		try {
			const response = await fetch(url + '/user/emailvalid', {
				method: 'POST',
				headers: {
					'Content-type': 'application/json',
				},
				body: JSON.stringify(loginData),
			});
			const result = await response.json();

			//    
			if (result.message === '   .') {
				if (!isEmail) {
					setMsgEmail('  .');
				} else if (!isPw) {
					setMsg(' 6  .');
				} else {
					setMsg(result.message);
					setIsDisabled(false);
					navigate('/join/profile', {
						state: {
							id: id,
							pw: pw,
						},
					});
				}
			} else if (
				result.message === '    .' ||
				!isEmail ||
				!isPw
			) {
				e.preventDefault();
				setIsDisabled(true);
				setIsEmail(false);
				setMsg(result.message);
			} else {
				setIsWrong(true);
				setIsDisabled(true);
				setMsg(result.message);
			}
		} catch (error) {
			console.error(error);
		}
	};

	// id input
	const handleIdInput = (e) => {
		setId(e.target.value);
		checkBtn();
		emailValidation(e.target.value);
	};

	// pw input
	const handlePasswordInput = (e) => {
		setPw(e.target.value);
		checkBtn();
		pwValidation(e.target.value);
	};

	return (
		<>
			<FormWrap onSubmit={handleJoinSubmit}>
				<InputDiv>
					<LabelStyle htmlFor="id"></LabelStyle>
					<InputStyle
						type="email"
						id="id"
						placeholder="   "
						onChange={handleIdInput}
						className={`${!isEmail || isWrong ? 'error' : ''}`}
						value={id}
						required
					/>
					<ErrorText>{msgEmail}</ErrorText>
				</InputDiv>
				<InputDiv>
					<LabelStyle htmlFor="password"></LabelStyle>
					<InputStyle
						type="password"
						id="password"
						placeholder="  ."
						onChange={handlePasswordInput}
						className={`${!isPw || isWrong ? 'error' : ''}`}
						value={pw}
						required
					/>
				</InputDiv>
				<ErrorText>{msg}</ErrorText>
				<BigBtnDiv>
					<BigBtn
						disabled={isDisabled ? 'disabled' : ''}
						className={`${isDisabled ? 'disabled' : ''}`}
						type="submit"
					>
						
					</BigBtn>
				</BigBtnDiv>
			</FormWrap>
		</>
	);
}

export default JoinForm;
