import React from 'react';
import { Link } from 'react-router-dom';
import {
	TitleStyle,
	Maintitle,
	JoinTitleP,
	JoinTitleLink,
} from './SplashTitle.style';
import {
	LoginBtnDiv,
	EmailBtn,
	KakaoBtn,
	GoggleBtn,
	FacebookBtn,
} from './SplashButton.style';

function SplashLogin() {
	return (
		<>
			<TitleStyle>
				<Maintitle>!</Maintitle>
				<Maintitle> .</Maintitle>
			</TitleStyle>

			<LoginBtnDiv>
				<EmailBtn as={Link} to="/login">
					 
				</EmailBtn>
				<KakaoBtn type="button">  </KakaoBtn>
				<GoggleBtn type="button">  </GoggleBtn>
				<FacebookBtn type="button">
					  
				</FacebookBtn>
			</LoginBtnDiv>

			<JoinTitleP>
				  ?
				<JoinTitleLink to="/join"></JoinTitleLink>
			</JoinTitleP>
		</>
	);
}

export default SplashLogin;
