import React, { useState, useEffect } from 'react';
import {
	InputDiv,
	LabelStyle,
	InputStyle,
	ErrorText,
} from '../common/Input.style';
import FormWrap from '../common/FormWrap.style';
import { BigBtnDiv, BigBtn } from '../common/Button.style';
import { useNavigate } from 'react-router-dom';

function LoginForm() {
	const [id, setId] = useState('');
	const [pw, setPw] = useState('');

	const [isWrong, setIsWrong] = useState(false);
	const [msg, setMsg] = useState('');
	const [isDisabled, setIsDisabled] = useState(true);
	const [isEmail, setIsEmail] = useState(true);

	const navigate = useNavigate();

	const url = 'https://mandarin.api.weniv.co.kr';

	const loginData = {
		user: {
			email: id,
			password: pw,
		},
	};

	const localStorage = window.localStorage;

	//   
	function checkEmail(target) {
		const emailRegex =
			/([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
		const emailCurrent = target;
		if (!emailRegex.test(emailCurrent)) {
			setMsg('  .');
			setIsEmail(false);
		} else {
			setMsg('');
			setIsEmail(true);
		}
	}

	// id, pw      
	useEffect(() => {
		if (id && pw) {
			setIsDisabled(false);
		} else {
			setIsDisabled(true);
		}
	}, [id, pw]);

	// useEffect    
	useEffect(() => {
		if (isEmail) {
			setIsEmail(true);
		} else {
			setIsEmail(false);
		}
	}, [id, pw]);

	//    
	const handleLoginSubmit = async (e) => {
		e.preventDefault();
		try {
			//  API  (id, pw  )
			const response = await fetch(url + '/user/login', {
				method: 'POST',
				headers: {
					'Content-type': 'application/json',
				},
				body: JSON.stringify(loginData),
			});
			const result = await response.json();

			//  
			if (result.status !== 422) {
				localStorage.setItem('user', JSON.stringify(result.user));
				//     JSON.parse(localStorage.getItem('user'));
				setIsWrong(false);
				setMsg('');
				navigate('/feed');
			} else {
				//  
				setMsg(result.message);
				setIsWrong(true);
			}
		} catch (error) {
			console.log(error);
		}
	};

	// id input
	const handleIdInput = (e) => {
		setId(e.target.value);
		checkEmail(e.target.value);
	};

	// pw input
	const handlePasswordInput = (e) => {
		setPw(e.target.value);
	};

	return (
		<>
			<FormWrap onSubmit={handleLoginSubmit}>
				<InputDiv>
					<LabelStyle htmlFor="id" className="hide">
						
					</LabelStyle>
					<InputStyle
						type="email"
						id="id"
						placeholder=" ."
						onChange={handleIdInput}
						className={`${!isEmail || isWrong ? 'error' : ''}`}
						value={id}
						required
					/>
				</InputDiv>
				<InputDiv>
					<LabelStyle htmlFor="password" className="hide">
						
					</LabelStyle>
					<InputStyle
						type="password"
						id="password"
						placeholder=" ."
						onChange={handlePasswordInput}
						className={`${isWrong ? 'error' : ''}`}
						value={pw}
						required
					/>
					<ErrorText>{msg}</ErrorText>
				</InputDiv>
				<BigBtnDiv>
					<BigBtn
						disabled={isDisabled}
						className={`${isDisabled ? 'disabled' : ''}`}
						type="submit"
					>
						
					</BigBtn>
				</BigBtnDiv>
			</FormWrap>
		</>
	);
}

export default LoginForm;
